.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.text-block-with-swiper {
  overflow: hidden;
  padding-top: 100px;
  padding-bottom: 90px;
}
@media (max-width: 960px) {
  .text-block-with-swiper {
    padding-top: 45px;
    padding-bottom: 45px;
  }
}
@media (max-width: 960px) and (max-width: 600px) {
  .text-block-with-swiper {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.text-block-with-swiper_background-color {
  background-color: #fbfbfb;
}
.text-block-with-swiper__container {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}
@media (max-width: 960px) {
  .text-block-with-swiper__container {
    flex-direction: column-reverse;
  }
}
.text-block-with-swiper__container .text-block-with-swiper__text-block {
  max-width: 527px;
  width: 100%;
}
@media (max-width: 960px) {
  .text-block-with-swiper__container .text-block-with-swiper__text-block {
    margin-left: auto;
    margin-right: auto;
  }
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__title-h2 {
  letter-spacing: 0.02em;
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__title-h6 {
  margin-top: 17px;
  letter-spacing: 0.02em;
}
@media (max-width: 600px) {
  .text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__title-h6 {
    margin-top: 10px;
  }
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__text {
  font-family: "Raleway";
  font-weight: 500;
  font-size: 16px;
  line-height: 52px;
  word-wrap: 0.01em;
  letter-spacing: 0em;
  line-height: 26px;
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-top: 9px;
  padding-right: 30px;
  padding-top: 10px;
  max-height: 318px;
  height: 100%;
  overflow-y: scroll;
}
@media (max-width: 600px) {
  .text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block {
    margin-top: 5px;
  }
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block::-webkit-scrollbar {
  width: 6px;
  border: 2px solid rgba(255, 255, 255, 0);
  box-sizing: border-box;
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block::-webkit-scrollbar-track {
  border-radius: 10px;
  box-sizing: content-box;
  border: 2px solid rgba(255, 255, 255, 0);
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1);
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block::-webkit-scrollbar-thumb {
  background: #337d5a;
  border-radius: 10px;
  position: absolute;
}
.text-block-with-swiper__container .text-block-with-swiper__text-block .text-block-with-swiper__scroll-block::-webkit-scrollbar-thumb:hover {
  background: #93b474;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block {
  position: relative;
  margin-top: auto;
  margin-bottom: auto;
  overflow: hidden;
  max-width: 700px;
  max-height: 472px;
  width: 100%;
  height: 100%;
}
@media (max-width: 960px) {
  .text-block-with-swiper__container .text-block-with-swiper__swiper-block {
    margin: auto;
  }
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .buttons-block {
  position: absolute;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  bottom: 0;
  left: 0;
  max-width: 141px;
  max-height: 58px;
  width: 100%;
  height: 100%;
  background-color: #337d5a;
  z-index: 2;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .buttons-block .swiper-button-next {
  position: relative;
  color: #ffffff;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  right: 0;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .buttons-block .swiper-button-next::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 22px;
  position: relative;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .buttons-block .swiper-button-prev {
  position: relative;
  color: #ffffff;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .buttons-block .swiper-button-prev::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 22px;
  transform: rotate(180deg);
  position: relative;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .swiper-slide.text-block-with-swiper__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  user-select: none;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .swiper-slide.text-block-with-swiper__slide.video:hover:after {
  color: #337d5a;
  border-color: #337d5a;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .swiper-slide.text-block-with-swiper__slide.video::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.text-block-with-swiper__container .text-block-with-swiper__swiper-block .swiper-slide.text-block-with-swiper__slide.video::after {
  font-family: icomoon;
  content: "\e924";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-45%, -39%);
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-left: 4px;
  width: 61px;
  height: 61px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  z-index: 4;
  cursor: pointer;
}
@media (max-width: 600px) {
  .text-block-with-swiper__container .text-block-with-swiper__swiper-block .swiper-slide.text-block-with-swiper__slide.video::after {
    width: 45px;
    height: 45px;
  }
}

.title-h2 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 45px;
  letter-spacing: 0.01em;
}
@media (max-width: 600px) {
  .title-h2 {
    font-size: 25px;
    line-height: 25px;
  }
}

.title-h6 {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  letter-spacing: 0.01em;
  letter-spacing: 0.01em;
  margin: 0;
}
@media (max-width: 600px) {
  .title-h6 {
    font-size: 16px;
    line-height: 20px;
  }
}