@import "./default/default.scss";

.text-block-with-swiper {
  overflow: hidden;
  padding-top: 100px;
  padding-bottom: 90px;
  @media (max-width: $tablet) {
    padding-top: 45px;
    padding-bottom: 45px;
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }
  
  &_background-color {
    background-color: #fbfbfb;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    
    @media (max-width: $tablet) {
      flex-direction: column-reverse;
    }
    .text-block-with-swiper__text-block {
      max-width: 527px;
      width: 100%;
      @media (max-width: $tablet) {
        margin-left: auto;
        margin-right: auto;
      }
      .text-block-with-swiper__title-h2 {
        letter-spacing: 0.02em;
        
      }
      .text-block-with-swiper__title-h6 {
        margin-top: 17px;
        letter-spacing: 0.02em;
        @media (max-width: $mobile) {
          margin-top: 10px;
        }
      }
      .text-block-with-swiper__text {
        @include rale-500-16;
        word-wrap: 0.01em;
        letter-spacing: 0em;
        line-height: 26px;
      }
      .text-block-with-swiper__scroll-block {
        display: flex;
        flex-direction: column;
        gap: 25px;
        margin-top: 9px;
        padding-right: 30px;
        padding-top: 10px;
        max-height: 318px;
        height: 100%;
        overflow-y: scroll;
        @media (max-width: $mobile) {
          margin-top: 5px;
        }
        &::-webkit-scrollbar {
          width: 6px;
          border: 2px solid rgba($color: #ffffff, $alpha: 0);
          box-sizing: border-box;
        }
        &::-webkit-scrollbar-track {
          border-radius: 10px;
          box-sizing: content-box;
          border: 2px solid rgba($color: #ffffff, $alpha: 0);
          box-shadow: inset 0 0 5px rgba($color: #000000, $alpha: 0.1);
        }
        &::-webkit-scrollbar-thumb {
          background: $darkGreen;
          border-radius: 10px;
          position: absolute;
        }
        &::-webkit-scrollbar-thumb:hover {
          background: $green;
        }
      }
    }
    .text-block-with-swiper__swiper-block {
      position: relative;
      margin-top: auto;
      margin-bottom: auto;
      overflow: hidden;
      max-width: 700px;
      max-height: 472px;
      width: 100%;
      height: 100%;
      @media (max-width: $tablet) {
        margin: auto;
      }
      .swiper-wrapper.text-block-with-swiper__wrapper {
      }
      .buttons-block {
        position: absolute;
        display: flex;
        flex-direction: row-reverse;
        align-items: center;
        justify-content: center;
        bottom: 0;
        left: 0;
        max-width: 141px;
        max-height: 58px;
        width: 100%;
        height: 100%;
        background-color: $darkGreen;
        z-index: 2;
        .swiper-button-next {
          position: relative;
          color: $white;
          width: 100%;
          height: 100%;
          margin: 0;
          padding: 0;
          top: 0;
          right: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 22px;
            position: relative;
          }
        }
        .swiper-button-prev {
          position: relative;
          color: $white;
          width: 100%;
          height: 100%;
          margin: 0;
          padding: 0;
          top: 0;
          left: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 22px;
            transform: rotate(180deg);
            position: relative;
          }
        }
      }
      .swiper-slide.text-block-with-swiper__slide {
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          user-select: none;
        }
        &.video {
          &:hover {
            &:after {
              color: $darkGreen;
              border-color: $darkGreen;
            }
          }
          &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            cursor: pointer;
          }
          &::after {
            font-family: icomoon;
            content: "\e924";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-45%, -39%);
            color: $white;
            display: flex;
            justify-content: center;
            align-items: center;
            padding-left: 4px;
            width: 61px;
            height: 61px;
            border: 2px solid $white;
            border-radius: 50%;
            z-index: 4;
            cursor: pointer;
            @media (max-width: $mobile) {
              width: 45px;
              height: 45px;
            }
          }
        }
      }
    }
  }
}

.title-h2 {
  @include rale-800-45;
}

.title-h6 {
  @include rale-600-24;
  letter-spacing: 0.01em;

  margin: 0;
}
